<template>
    <div>
        <div class="titulo_tabla">
            <h3>Productos</h3>
        </div>
    </div>

    <div>
        <form class="form-container" @submit.prevent="guardar">
            <div class="form-group">
                <label for="idProducto">ID:</label><!-- Etiqueta para escritura -->
                <input type="text" id="idProducto" name="idProducto" v-model="idProducto" required />
            </div>
            <div class="form-group">
                <label for="nombre">Nombre:</label><!-- Etiqueta para escritura -->
                <input type="text" id="nombre" name="nombre" v-model="nombre" required />
            </div>
            <div class="form-group">
                <label for="precioVenta">Precio:</label><!-- Etiqueta para escritura -->
                <input type="number" id="precioVenta" name="precioVenta" v-model="precioVenta" required />
            </div>
            <div class="form-group">
                <label for="cantidad">Cantidad:</label><!-- Etiqueta para escritura -->
                <input type="number" id="cantidad" name="cantidad" v-model="cantidad" required />
            </div>
            <div class="form-group">
                <label for="proveedor">Proveedor:</label><!-- Etiqueta para escritura -->
                <select v-model="proveedor" required>
                    <option v-for="proveedor in proveedores" :key="proveedor.idProveedor" type="text" id="proveedor"
                        name="proveedor" :value="proveedor.idProveedor">
                        {{ proveedor.idProveedor + " - " + proveedor.nombre }} </option>
                </select>
            </div>
            <div class="form-group">
                <label for="categoria">Categoria:</label><!-- Etiqueta para escritura -->
                <select v-model="categoria" required>
                    <option v-for="categoria in categorias" :key="categoria.idCategoria" type="text" id="categoria"
                        name="Categoria" :value="categoria.idCategoria">
                        {{ categoria.idCategoria + " - " + categoria.nombre }}
                    </option>
                </select>
            </div>
            <div class="form-group">
                <label for="costoProducto">Costo:</label><!-- Etiqueta para escritura -->
                <input type="number" id="costoProducto" name="costoProducto" v-model="costoProducto" required />
            </div>
            <div class="form-actions">
                <button type="submit" id="guardar" name="guardar" class="btn-submit">Registrar</button>
                <button type="submit" class="btn-crud" name="consultar" id="consultar"
                    @click="consultar">Consultar</button>
                <button type="button" class="btn-crud" name="eliminar" id="eliminar" @click="eliminar">Eliminar</button>
                <button type="button" class="btn-crud" name="actualizar" id="actualizar"
                    @click="actualizar">Actualizar</button>
            </div>
        </form>
    </div>
    <div>

        <div class="table-container">
            <table>
                <thead>
                    <tr>
                        <th> Id </th>
                        <th> Nombre </th>
                        <th> Precio </th>
                        <th> Cantidad </th>
                        <th> Proveedor </th>
                        <th> Categoria </th>
                        <th> Costo </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="producto in productos" :key="producto.idProducto">
                        <td>{{ producto.idProducto }}</td>
                        <td>{{ producto.nombre }}</td>
                        <td>{{ producto.precioVenta }}</td>
                        <td>{{ producto.cantidad }}</td>
                        <td>{{ producto.proveedor.nombre }}</td>
                        <td>{{ producto.categoria.nombre }}</td>
                        <td>{{ producto.costoProducto }}</td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import swal from 'sweetalert2'

export default {

    data() {

        return {
            proveedores: [],
            categorias: [],
            productos: [],
            idProducto: "",
            nombre: "",
            precioVenta: "",
            cantidad: "",
            proveedor: "",
            categoria: "",
            costoProducto: "",
            contadorerrores: 0,
            maxErrores: 5,
        };
    },

    methods: {
        consultar_Proveedor() {
            axios
                .get("https://bonsterservice-production.up.railway.app/api/proveedor/listar")
                .then((response) => {
                    this.proveedores = response.data;
                    this.contadorerrores = 0;
                })
                .catch((error) => {

                    this.contadorerrores++;

                    console.error("error al cargar Proveedor", error);
                    if (this.contadorerrores >= this.maxErrores) {
                        swal.fire({
                            icon: 'error',
                            title: 'Servidor no responde',
                            text: 'Demasiados errores al intentar conectar al servidor! No: ' + this.contadorerrores,
                        });

                    } else {
                        swal.fire({
                            icon: 'error',
                            title: 'Error',
                            text: 'Hubo un error al cargar proveedor! No: ' + this.contadorerrores,
                        });

                    }
                })
                .finally(() => {

                    let tiempo = this.contadorerrores > 0 ? 30000 * this.contadorerrores : 30000;

                    if (this.contadorerrores < this.maxErrores) {
                        setTimeout(this.listarProveedor, tiempo);
                    }
                    else {
                        setTimeout(this.listarProveedor, tiempo);
                    }
                });

        },
        consultar_Categoria() {
            axios
                .get("https://bonsterservice-production.up.railway.app/api/categoria/listar")
                .then((response) => {
                    this.categorias = response.data;
                    this.contadorerrores = 0;
                })
                .catch((error) => {

                    this.contadorerrores++;

                    console.error("error al cargar categoria", error);
                    if (this.contadorerrores >= this.maxErrores) {
                        swal.fire({
                            icon: 'error',
                            title: 'Servidor no responde',
                            text: 'Demasiados errores al intentar conectar al servidor! No: ' + this.contadorerrores,
                        });

                    } else {
                        swal.fire({
                            icon: 'error',
                            title: 'Error',
                            text: 'Hubo un error al cargar categoria! No: ' + this.contadorerrores,
                        });

                    }
                })
                .finally(() => {

                    let tiempo = this.contadorerrores > 0 ? 30000 * this.contadorerrores : 30000;

                    if (this.contadorerrores < this.maxErrores) {
                        setTimeout(this.listarCategorias, tiempo);
                    }
                    else {
                        setTimeout(this.listarCategorias, tiempo);
                    }
                });

        },
        guardar() {

            //console.log("Proveedor seleccionado ", this.proveedor);
            //console.log("categoria seleccionado ", this.categoria);

            if (this.nombre.trim() === "") {
                swal.fire({
                    icon: 'error',
                    title: 'Oops...',
                    text: 'Nombre del Producto no puede estar vacío!',
                });
                return;
            }
            axios
                .post("https://bonsterservice-production.up.railway.app/api/producto", {

                    idProducto: this.idProducto,
                    nombre: this.nombre,
                    precioVenta: this.precioVenta,
                    cantidad: this.cantidad,
                    proveedor: {
                        idProveedor: this.proveedor
                    },
                    categoria: {
                        idCategoria: this.categoria
                    },
                    costoProducto: this.costoProducto,
                })
                .then((response) => {
                    swal.fire({
                        icon: 'success',
                        title: 'Producto resgitrado',
                        text: 'El Producto: ' + response.data.nombre + ' ha sido registrado correctamente!',
                    });

                    this.idProducto = "";
                    this.nombre = "";
                    this.precioVenta = "";
                    this.cantidad = "";
                    this.proveedor = "";
                    this.categoria = "";
                    this.costoProducto = "";
                })
                .catch((error) => {
                    swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: 'Hubo un error al registrar el producto!',
                    });
                    console.error("errror al registrar producto", error)
                });
        },
        consultar() {
            axios
                .get("https://bonsterservice-production.up.railway.app/api/producto/" + this.idProducto)
                .then((response) => {
                    swal.fire({
                        icon: 'success',
                        title: 'Id valido',
                        text: 'Producto: ' + response.data.idCategoria + 'encontrado correctamente!',
                    });

                    this.idProducto = response.data.idProducto;
                    this.nombre = response.data.nombre;
                    this.precioVenta = response.data.precioVenta;
                    this.cantidad = response.data.cantidad;
                    this.proveedor = response.data.proveedor;
                    this.categoria = response.data.categoria;
                    this.costoProducto = response.data.costoProducto;
                })
                .catch((error) => {
                    swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: 'Hubo un error al Listar el Id de el Prodcuto!',
                    });
                    console.error("errror al Consultar el Id del Producto", error)
                });
        },
        actualizar() {
            axios
                .put("https://bonsterservice-production.up.railway.app/api/producto/Actualizar/" + this.idProducto, {

                    idProducto: this.idProducto,
                    nombre: this.nombre,
                    precioVenta: this.precioVenta,
                    cantidad: this.cantidad,
                    proveedor: {
                        idProveedor: this.proveedor
                    },
                    categoria: {
                        idCategoria: this.categoria
                    },
                    costoProducto: this.costoProducto,
                })
                .then((response) => {
                    swal.fire({
                        icon: 'success',
                        title: 'Actualizacion Realizada',
                        text: 'Producto: ' + response.data.nombre + ' actualizado correctamente!',
                    });

                    this.idProducto = "";
                    this.nombre = "";
                    this.precioVenta = "";
                    this.cantidad = "";
                    this.proveedor = "";
                    this.categoria = "";
                    this.costoProducto = "";          
                })
                .catch((error) => {
                    swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: 'Hubo un error actualizar el Producto!',
                    });
                    console.error("errror al actualizar el Producto", error)
                });
        },
        eliminar() {
            axios
                .delete("http://localhost:8080/api/producto/" + this.idProducto)
                .then(() => {
                    swal.fire({
                        icon: 'success',
                        title: 'Producto eliminado',
                        text: 'Producto Eliminado Correctamente!',
                    });
                    this.idProducto = "";
                    this.nombre = "";
                    this.precioVenta = "";
                    this.cantidad = "";
                    this.proveedor = "";
                    this.categoria = "";
                    this.costoProducto = "";
                })
                .catch((error) => {
                    swal.fire({
                        icon: 'error',
                        title: 'Error',
                        text: 'Hubo un error al eliminar el Producto!',
                    });
                    console.error("error al eliminar el Producto", error)
                });

        },
        listarProductos() {
            axios
                .get("https://bonsterservice-production.up.railway.app/api/producto/listar")
                .then((response) => {
                    this.productos = response.data;
                    this.contadorerrores = 0;
                })
                .catch((error) => {

                    this.contadorerrores++;

                    console.error("error al cargar Producto", error);
                    if (this.contadorerrores >= this.maxErrores) {
                        swal.fire({
                            icon: 'error',
                            title: 'Servidor no responde',
                            text: 'Demasiados errores al intentar conectar al servidor! No: ' + this.contadorerrores,
                        });

                    } else {
                        swal.fire({
                            icon: 'error',
                            title: 'Error',
                            text: 'Hubo un error al cargar Producto! No: ' + this.contadorerrores,
                        });

                    }
                })
                .finally(() => {

                    let tiempo = this.contadorerrores > 0 ? 30000 * this.contadorerrores : 30000;

                    if (this.contadorerrores < this.maxErrores) {
                        setTimeout(this.listarProductos, tiempo);
                    }
                    else {
                        setTimeout(this.listarProductos, tiempo);
                    }
                });

        },
    },
    mounted() {
        this.consultar_Proveedor();
        this.consultar_Categoria();
        this.listarProductos();
    }

};
</script>

